import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-current-okr',
  templateUrl: './current-okr.component.html',
  styleUrls: ['./current-okr.component.scss']
})
export class CurrentOkrComponent implements OnInit {
  private  url = 'https://us-central1-okr-platform.cloudfunctions.net/routes-okrs';
  private http: HttpClient;
  private okrList;
  constructor() { }

  ngOnInit() {}

  public getOkrList() {
    this.http.get(this.url)
      .subscribe(data => {
        this.okrList = data;
      });
  }
}
